<template>
  <div style='background:#fff'>
    <common-header :h_title="h_title" :showback="true" :bgcolor='head_color'></common-header>
    <return-top @returnTop='goTop'></return-top>
    <div style='height:0.9rem'></div>
    <div class='bind'>
      <!-- <div class='b_jump'>
        <span @click='skipBind' :hidden="visited=='1'">跳过</span>
      </div> -->
      <div class='b_content'>
        <div class='b_title'>
          <span>绑定手机号</span>
        </div>
        <div class='b_tips'>
          <span>为了您的账号安全，请先绑定手机号</span>
        </div>
        <div class='b_form'>
            <div :class="isPhoneShowRed?'form-item showRed':'form-item showW'" style="padding-right:0.32rem">
                <img :src="phoneImg">
                <input type='number' style="padding-left: 0.16rem" v-model="mobile" @blur="noClose" placeholder="请输入11位手机号">
                <div class="close" v-show="mobile !== ''" @click="clearInfo">
                    <img :src="clearImg" alt="">
                </div>
            </div>
            <div :class="isCodeShowRed?'form-item showRed':'form-item showW'">
                <img :src="suoImg">
                <input type='number' style="padding-left: 0.16rem" @blur='noClose' v-model="code" placeholder="请输入6位验证码">
                <button :class="!isShowTime ? 'codeClick':'codeTime' " size="small" @click='send'>{{text}}</button>
                <!-- <button :class="!isShowTime ? 'codeClick':'codeTime' " v-if="!isShowTime" @click='send' class="codeClick">获取验证码</button>
                <button v-else :disabled='isDisabled' class="codeTime">{{text}}</button> -->
            </div>
          <!-- <form bindsubmit="formSubmit" bindreset="formReset"> -->
            <!-- <div class="section mobile">
              <span>+86</span>
              <input placeholder="输入手机号码" class='width' v-model="mobile"/>
            </div>
            <div class="section getcode">
              <input placeholder="输入验证码" @blur='getCode' v-model="code"/>
              <mt-button size="small" :disabled='isDisabled' @click='send' :style="{backgroundColor:color}">{{text}}</mt-button>
            </div> -->
            <!-- <div class="section next">
              <mt-button form-type='submit' size='normal' @click="formSubmit">完成</mt-button>
            </div>
            <div class="section tips">
              <p>
               点击完成表示您同意<span @click='goProtocol' class="tips_a">《公主购服务协议》</span>
              </p>
            </div> -->
          <!-- </form> -->
        </div>
        <div class="login">
          <button :class="!isLogin ? 'noLogin':'okLogin' " @click="formSubmit" v-if='visited==-1'>登录</button>
          <button :class="!isLogin ? 'noLogin':'okLogin' " @click="formSubmit" v-else>完成</button>
        </div>
         <div class="argessInfo" v-if="visited==-1">
            <div class="info">
                <p>点击登录表示您同意<span @click="goProtocol">《公主购服务协议》</span></p>
            </div>
          </div>
      </div>
    </div>
  </div>
</template>

<script>
import phoneIcon from '@/assets/img/icon-phone2.png'
import suoIcon from '@/assets/img/icon-suo.png'
import commonHeader from 'common/common-header'
import clearIcon from '@/assets/img/clear.png'
import {
  InfiniteScroll,
  Indicator,
  Button,
  Lazyload,
  Toast,
  MessageBox
} from 'mint-ui'
import {
  sendCode, savePhone
} from '@/api/index'
import {
  isGoLogin
} from '@/api/member-new'
import {
  mapMutations,
  mapGetters
} from 'vuex'
  // import {
  //   countDown
  // } from '@/store/pubilc.js'
  // import {
  //   parseTime
  // } from '@/store/pubilc.js'
export default {
  name: 'Bind',
  components: {
    commonHeader,
    InfiniteScroll,
    Indicator,
    Button,
    Lazyload,
    Toast,
    MessageBox
  },
  data() {
    return {
      h_title: '绑定手机',
      head_color: {
        background: '#f9f9f9',
        color: '#000'
      },
      visited: 0,
      mobile: '',
      code: '',
      text: '获取验证码',
      currentTime: 61,
      isDisabled: false,
      color: '#ff4e79',
      canclick: false,
      is_success: false,
      phoneImg: phoneIcon,
      suoImg: suoIcon,
      isClear: false,
      isLogin: false,
      isPhoneShowRed: false,
      isCodeShowRed: false,
      isShowTime: false,
      clearImg: clearIcon,
      openid: '',
      uionId: '',
      name: '',
      iconurl: ''
    }
  },
  mounted() {
    // Indicator.open('加载中...')
    console.log(this.$route.query)
    this.visited = this.$route.query.visit
    var storage = window.localStorage
    console.log(storage.getItem('login_phone'))
    if (storage.getItem('login_phone') != null) {
      this.mobile = storage.getItem('login_phone')
    }
    if (storage.getItem('login_code') != null) {
      this.code = storage.getItem('login_code')
    }
    if (this.visited == -1) {
      // 授权后绑定手机
      this.openid = this.$route.query.openid
      this.uionId = this.$route.query.unionid
      this.name = this.$route.query.name
      this.iconurl = this.$route.query.iconurl
    }
    this.noClose()
  },
  methods: {
    ...mapMutations({
      setIsbind: 'SET_ISBIND',
      setUserInfo: 'SET_USERINFO',
      setInviter: 'SET_INVITER',
      setMember: 'SET_MEMBER',
      setShowShare: 'SET_SHARE',
      setFirst: 'SET_FIRST',
      setScore: 'SET_SCORE'
    }),
    goTop() {
      console.log('回到顶部')
      document.getElementById('coll').scrollIntoView({
        block: 'start',
        behavior: 'smooth'
      })
    },
    noClose() {
      this.isClear = false
      if (this.mobile !== '' && this.mobile.length === 11 && /^1[3|4|5|6|7|8|9]\d{9}$/.test(this.mobile)) {
        this.isLogin = true
        this.isPhoneShowRed = true
      } else {
        this.isLogin = false
        this.isPhoneShowRed = false
      }

      if (this.code !== '' && this.code.length === 6) {
        this.isCodeShowRed = true
      } else {
        this.isCodeShowRed = false
      }
    },
    clearInfo() {
      // console.log(1)
      this.mobile = ''
      this.isLogin = false
      this.isPhoneShowRed = false
    },
    // skipBind: function () {
    //   var that = this
    //   if (that.visited === '3') {
    //     // 已注册未绑定手机
    //     wx.switchTab({
    //       url: '../index/index'
    //     })
    //   } else if (that.visited == '0') {
    //     // 扫码领赠品
    //     if (getApp().globalData.winId != '') {
    //       wx.redirectTo({
    //         url: '../myGift/myGift'
    //       })
    //     } else {
    //       wx.switchTab({
    //         url: '/' + getApp().globalData.backUrl
    //       })
    //       wx.redirectTo({
    //         url: '/' + getApp().globalData.backUrl
    //       })
    //     }
    //   } else if (that.visited == '4') {
    //     // 在商品购买页已注册未绑定手机
    //     wx.redirectTo({
    //       url: '/' + getApp().globalData.backUrl
    //     })
    //     wx.switchTab({
    //       url: '/' + getApp().globalData.backUrl
    //     })
    //   } else if (that.visited == '1') {
    //     // 个人信息修改手机号码
    //     setTimeout(function () {
    //       wx.navigateBack({
    //         delta: 1
    //       })
    //     }, 500)
    //   } else if (that.visited == '5') {
    //     // 个人中心点登陆,或者签到
    //     wx.switchTab({
    //       url: '/pages/memberCenter/memberCenter'
    //     })
    //   } else if (that.visited == '6') {
    //     // 抽奖页未绑定手机
    //     wx.redirectTo({
    //       url: '../lottery/lottery'
    //     })
    //   } else {
    //     // 返回上一页
    //     wx.navigateBack({
    //       delta: 1
    //     })
    //   }
    // },
    // getCode: function (e) {
    //   // this.canclick = false
    //   // code: e.detail.value

    // },
    send() {
      console.log(0)
      var that = this
      that.isDisabled = true // 只要点击了按钮就让按钮禁用 （避免正常情况下多次触发定时器事件）
      that.color = '#ccc'
      var phone = that.mobile
      var currentTime = that.currentTime
      var warn = null // warn为当手机号为空或格式不正确时提示用户的文字，默认为空
      if (phone === '') {
        warn = '号码不能为空'
      } else if (phone.trim().length !== 11 || !/^1[3|4|5|6|7|8|9]\d{9}$/.test(phone)) {
        warn = '手机号格式不正确'
      } else {
        Indicator.open('发送中')
        // 当手机号正确的时候提示用户短信验证码已经发送,并后台请求短信
        sendCode(that.mobile).then(res => {
          this.isShowTime = true
          Indicator.close()
          var interval = setInterval(function () {
            currentTime-- // 每执行一次让倒计时秒数减一
            that.color = '#ccc'
            that.text = currentTime + 's' // 按钮文字变成倒计时对应秒数
            if (currentTime <= 0) {
              clearInterval(interval)
              that.text = '重新发送'
              that.currentTime = 61
              that.isDisabled = false
              that.color = '#ff4e79'
              that.isShowTime = false
            }
          }, 1000)
          if (res.isSuccess) {
            Toast({
              message: res.msg,
              duration: 1000
            })
          } else {
            clearInterval(interval)
            that.isDisabled = false
            that.color = '#ff4e79'
            Toast({
              message: res.msg,
              duration: 1000
            })
          }
        })
      }
      // 判断 当提示错误信息文字不为空 即手机号输入有问题时提示用户错误信息 并且提示完之后一定要让按钮为可用状态 因为点击按钮时设置了只要点击了按钮就让按钮禁用的情况
      if (warn != null) {
        let plus = window.plus
        if (window.plus) {
          plus.nativeUI.confirm(warn, function(e) {
            console.log('Close confirm: ' + e.index)
            if (e.index === 0 || e.index === '0') {
              console.log('用户点击确定')
            }
          }, {
            'title': '提示',
            'buttons': ['确定'],
            'verticalAlign': 'center'
          })
        } else {
          MessageBox('提示', warn)
        }
        // wx.showModal({
        //   title: '提示',
        //   content: warn
        // })
        that.isDisabled = false
        that.color = '#ff4e79'
      }
    },
    goProtocol() {
      var storage = window.localStorage
      if (this.mobile != '') {
        storage.setItem('login_phone', this.mobile)
      }
      if (this.code != '') {
        storage.setItem('login_code', this.code)
      }
      this.$router.togo({
        path: '/protocol'
      })
    },
    formSubmit: function () {
      if (this.mobile === '') {
        if (this.mobile.trim().length !== 11 || !/^1[3|4|5|6|7|8|9]\d{9}$/.test(this.mobile)) {
          Toast({
            message: '手机号格式不正确',
            duration: 1000
          })
        //   wx.showToast({
        //     title: '手机号格式不正确',
        //     icon: 'none',
        //     duration: 1000
        //   })
        }
        // wx.showToast({
        //   title: '手机号不能为空',
        //   icon: 'none',
        //   duration: 1000
        // })
      } else if (this.code === '') {
        Toast({
          message: '验证码不能为空',
          duration: 1000
        })
        // wx.showToast({
        //   title: '验证码不能为空',
        //   icon: 'none',
        //   duration: 1000
        // })
      } else {
        // wx.showLoading({
        //   title: '提交中'
        // })
        Indicator.open('提交中')
        if (!this.is_success) {
          var that = this
          let storage = window.localStorage
          if (this.visited == -1) {
            // 微信授权绑定手机号
            let info = {
              mobile: that.mobile,
              tel_verify: that.code,
              openid: that.openid,
              uionId: that.uionId,
              name: that.name,
              iconurl: that.iconurl
            }
            isGoLogin(info).then(res => {
              Indicator.close()
              if (res.isSuccess) {
                storage.removeItem('login_phone')
                storage.removeItem('login_code')
                var data =
                    'uid=' +
                    res.uid +
                    ';username=' +
                    res.username +
                    ';rank=' +
                    res.rank +
                    ';token=' +
                    res.token +
                    ';source=' +
                    res.source +
                    ';sessionid=' +
                    res.sessionid
                console.log(data)
                let userInfo = {}
                userInfo.avatar = res.image
                userInfo.level = res.rankName
                userInfo.isShow = 1
                userInfo.nickname = res.nickname
                userInfo.isbind = res.bindPhone
                userInfo.level_id = res.level_id
                userInfo.level_name = res.levelName
                userInfo.inviter = res.inviter
                userInfo.is_first = res.is_first
                that.setUserInfo(userInfo)
                that.setInviter(res.inviter)
                storage.setItem('token', data)
                storage.setItem('isLogin', true)
                that.setIsbind(res.bindPhone)
                that.userInfo = that.$store.state.home.userInfo
                if (res.level_id !== 0 && res.level_id !== '0') {
                  that.setMember(true)
                } else {
                  that.setMember(false)
                }
                console.log('返回上一页')
                if (res.bindPhone === true) {
                  // 跳转首页
                  that.$router.replace({
                    path: '/index'
                  })
                } else {
                  that.$router.togo({
                    path: '/bind?visit=0'
                  })
                }
              } else {
                Toast({
                  message: res.msg,
                  duration: 1000
                })
              }
            })
          } else {
          // 保存手机号 接口
            let info = {
              phone: that.mobile,
              code: that.code
            }
            savePhone(info).then(res => {
              Indicator.close()
              if (res.isSuccess) {
                that.is_success = true
                Toast({
                  message: res.msg,
                  duration: 500
                })
                that.setIsbind(true)
                if (that.visited === '3') {
                  // 签到已注册未绑定手机
                // wx.redirectTo({
                //   url: '../mySignIn/mySignIn'
                // })
                  that.$router.replace({
                    path: '/mySignIn'
                  })
                } else if (that.visited === '0') {
                  setTimeout(function() {
                    that.$router.back()
                  }, 500)
                  // 扫码中奖前往领取
                // if (getApp().globalData.winId != '') {
                //   wx.redirectTo({
                //     url: '../myGift/myGift'
                //   })
                // } else {
                //   wx.switchTab({
                //     url: '/' + getApp().globalData.backUrl
                //   })
                //   wx.redirectTo({
                //     url: '/' + getApp().globalData.backUrl
                //   })
                // }
                } else if (that.visited === '4') {
                  setTimeout(function() {
                    that.$router.back()
                  }, 500)
                  // 在商品购买页，试用页面，我的赠品，我的奖品，我的试用已注册未绑定手机
                // wx.redirectTo({
                //   url: '/' + getApp().globalData.backUrl
                // })
                // wx.switchTab({
                //   url: '/' + getApp().globalData.backUrl
                // })
                //     that.$router.replace({
                //       path:'/member'
                //   })
                } else if (that.visited === '1') {
                  // 个人信息修改手机号码
                  setTimeout(function() {
                    that.$router.back()
                  }, 500)
                } else if (that.visited === '5') {
                  // 个人中心点登陆,或者签到
                // wx.switchTab({
                //   url: '/pages/memberCenter/memberCenter'
                // })
                  that.$router.replace({
                    path: '/member'
                  })
                } else if (that.visited === '6') {
                  // 抽奖页未绑定手机
                  that.$router.replace({
                    path: '/lottery'
                  })
                // wx.redirectTo({
                //   url: '../lottery/lottery'
                // })
                } else {
                  // 返回上一页
                  that.$router.go(-2)
                }
              } else {
                Toast({
                  message: res.msg,
                  duration: 1000
                })
              }
            })
          }
        }
      }
    }
  },
  computed: {
    ...mapGetters([])
  }
}

</script>

