.login{
            .noLogin{
                width:100%;
                height:92/100rem;
                background:linear-gradient(90deg,rgba(231,232,239,1) 0%,rgba(202,204,218,1) 100%);
                border-radius:46/100rem;
                font-size: 36/100rem;
                font-weight: 400;
