  }

  .b_form .next .btn_default {
    background: #c7c7c7;
  }

  .argessInfo{
            .info{
                height: 34/100rem;
                width: 100%;
                margin-bottom: 36/100rem;
                margin-top: 20/100rem;
                p{
                    width: 100%;
                    line-height: 34/100rem;
                    font-size: 24/100rem;
                    color:rgba(0,0,0,0.5);
                    text-align: center;
                    span{
                        color:#FB560A;
                    }
                }
            }
        }

</style>



